import {Cover1} from '../../graphics/imageCover/Cover'
import Text from '../../graphics/textDisplayer/Text'

export default function AlphaDisplayer({children, style, height, width, image, title, description, buttonText, clickAction}) {
    
    const textStyle = {
        titleStyle : "text-white text-xl font-bold text-center",
        descriptionStyle : "text-white text-center",
        buttonStyle : "",
    }
    
    return (
        <div className="relative flex overflow-hidden my-4 mx-4 bg-gray-900">
            <img className="object-cover object-center" src={image} alt="image cover" />
            <div className="absolute flex justify-center items-end w-full h-full b">
                <div className="flex flex-col justify-center items-center  w-10/12 h-4/12">
                    <h2 className={textStyle.titleStyle}>{title}</h2>
                    <p className={textStyle.descriptionStyle}>{description}</p>
                    <div className="flex justify-center items-center m-5 bg-white w-4/12 h-2/12 "><p className={textStyle.buttonStyle}>{buttonText}</p></div>                
                </div>
            </div>
        </div>
    )
} 